.p {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 242);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden; /*not: içerisine eklenecek image'ler overflow olmasın diye.*/
}

.p-browser {
  height: 20px;
  background-color: rgb(243, 242, 242);
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 2;
}

.p-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
}

.p-img{
    width: 100%;
    transition: all 1s ease;
}

.p:hover .p-img{
  transform: translateY(-30%); /*height'ine göre dinamik classname verip, burda açıyı ona göre verebilirsin.*/
}

@media screen and (max-width: 480px){

  .p{
    width: 40%;
    height: 20vh;
  }
}